<template>
  <div class="tu">
    <div class="echarts-bg">
      <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
    </div>
    <el-row :gutter="20">
      <el-col :span="12">
        <div class="echarts-bg">
          <div id="myChart1" :style="{width: '100%', height: '300px'}"></div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="echarts-bg">
          <div id="myChart2" :style="{width: '100%', height: '300px'}"></div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<style>
.tu {
  padding: 15px;
  box-sizing: border-box;
}
.sub {
  display: flex;
  margin-top: 10px;
}
.mid {
  width: 2%;
}
.tu .echarts-bg:first-child{
  margin-bottom: 20px;
}
.echarts-bg {
  background: #ffffff;
  box-sizing: border-box;
  padding: 10px;
}
</style>

<script>
export default {
  name: "eCharts",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(document.getElementById("myChart"));
      var myChart1 = this.$echarts.init(document.getElementById("myChart1"));
      var myChart2 = this.$echarts.init(document.getElementById("myChart2"));
      // 绘制图表
      myChart.setOption({
        backgroundColor: "#fff",
        title: { text: "总转诊量趋势图（人次）" },
        tooltip: {},
        legend: {
          x: "350px",
          y: "5px",
          data: ["住院", "门诊"]
        },
        xAxis: {
          data: [
            "2018.01",
            "2018.02",
            "2018.03",
            "2018.04",
            "2018.05",
            "2018.06",
            "2018.07",
            "2018.08",
            "2018.09",
            "2018.10",
            "2018.11",
            "2018.12"
          ]
        },
        yAxis: {},
        series: [
          {
            name: "住院",
            type: "line",
            data: [110, 90, 109, 129, 110, 155, 188, 78, 89, 90, 110, 150]
          },
          {
            name: "门诊",
            type: "line",
            data: [61, 63, 61, 74, 75, 69, 90, 90, 90, 84, 97, 96]
          }
        ]
      });

      myChart1.setOption({
        backgroundColor: "#fff",
        title: { text: "上海杨思医院 总转诊量趋势图（人次）" },
        tooltip: {},
        legend: {
          x: "350px",
          y: "5px",
          data: ["住院", "门诊"]
        },
        xAxis: {
          data: ["第一季度", "第二季度", "第三季度", "第四季度"]
        },
        yAxis: {},
        series: [
          {
            name: "住院",
            type: "line",
            data: [65, 35, 78, 65]
          },
          {
            name: "门诊",
            type: "line",
            data: [61, 53, 61, 64]
          }
        ]
      });

      myChart2.setOption({
        backgroundColor: "#fff",
        title: { text: "上海安达医院 总转诊量趋势图（人次）" },
        tooltip: {},
        legend: {
          x: "350px",
          y: "5px",
          data: ["住院", "门诊"]
        },
        xAxis: {
          data: ["第一季度", "第二季度", "第三季度", "第四季度"]
        },
        yAxis: {},
        series: [
          {
            name: "住院",
            type: "line",
            data: [55, 45, 68, 55]
          },
          {
            name: "门诊",
            type: "line",
            data: [61, 63, 61, 74]
          }
        ]
      });
    }
  }
};
</script>


